*, *::before, *::after{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: sans-serif;
}
.prompt-container{
    height:100vh;
    width:100vw;
    background:rgb(48, 218, 122);
    padding:45px 65px
}
.input{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.inputs{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-auto-rows:minmax(110px,auto);
    flex-wrap:wrap;
 background:white;
 max-width:1100px;
 margin:0 auto;
 position:absolute;
 top:50%;
 left:50%;
 transform:translate(-50%,-50%);
 border-radius:15px;
 padding:0 25px

    
}
.inputs input, .inputs textarea{
    height:33px;
    margin:8px 0;
    width:200px
   
}

input{
    border: 1px solid rgb(186, 186, 186);

}
.profile{
    grid-column:span 3;
}
.profile textarea{
    width:100%;
    height:75px;
    border:1px solid black;
    border-radius:10px;
    text-align:center
}
input::placeholder, textarea::placeholder {
    text-align:center;
}

input, textarea{

    padding-left:10px;
    outline:none;
    cursor:pointer;
}
textarea{
    resize:none;
    
    
}
.row{
    display:flex;
    justify-content: center;
    align-items: center;
    padding-left:25px;
    width:100%

    
}
label{
    font-size:13px;
    color:rgb(149, 149, 149);
    cursor:pointer;
    transition:0.3s;
}
label:hover{
   
    color:rgb(101, 100, 100)
}
.row > * {
    margin:5px;
    
}

.generate-button{
    grid-column-start:1;
    grid-column-end: -1;
    height:35px;
    width:50%;
    margin:auto
}

@media (max-width:700px){
 .inputs{
    grid-template-columns: repeat(2,1fr);
 }
}
@media (max-width:500px){
    .inputs{
        grid-template-columns: 1fr;
    }
}